$(document).ready(function(){
  $('.header__nav').click(function(){
    $('.header__nav').toggleClass('active')
  })
})

$(document).ready(function(){
  $('.category__main li').click(function(event){
    $(this).children('ul').toggleClass('active');
    event.stopPropagation();
  })
})
